<template>
    <div class="Succeed">
      <HomePageTop :TopTitle="TopTotle" class="bg_fff"></HomePageTop>
      <div style="" class="SucceedTop" @click="imgs=!imgs">
        <img :src="imgs==true?imgHome2:imgHome1" alt="">

        <p  v-if="imgs">提交成功</p>
        <p  v-else>提交失败</p>

      </div>
      <div class="public-btn mt60 mr30 ml30" v-if="imgs">查看列表详情</div>
      <div class="public-btn mt60 mr30 ml30" v-else>重新提交</div>
      <router-link :to="{path:'FillMessage'}"  style="" class="getBack">返回</router-link>

    </div>
</template>
<script>
    import Vue from "vue"
    import HomePageTop from "../../common/HomePageTop";

    export default {
        name: "Succeed",
        components: {HomePageTop},
        data() {
            return {
              //像子组件传值
              TopTotle: {name: "提交状态",left: true},
              imgs:true,
              imgHome1:require("../../../assets/img/common/bedefeated.png"),
              imgHome2:require("../../../assets/img/common/succeed.png")

            }
        },
        methods: {},
        created() {
        }
    }

</script>

<style scoped lang="less">
  .Succeed{
    .SucceedTop{
      width: 100%;height: 4.72rem;text-align: center;background: #fff;padding-top: 1.4rem;
      img{width: 1.28rem; margin: .32rem;}
      p{text-align: center;font-size:.34rem;color: #333}

    }
    .getBack{width:6.9rem;height: .96rem;background:#fff;border: 1px solid #f90;text-align: center;color: #f90;line-height: .96rem;font-size: .34rem;margin: .32rem auto;display: block}
  }
  html, both {
    background: #F4F6F9;
  }
  .HomePageTop .Head {
    background: #fff;
  }
</style>



